@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,700);
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  z-index: 10;
}

.modal-body {
  position: fixed;
  background: white;
  width: 50%;
  height: auto;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  border-radius: 5px;
  max-height: calc(100vh - 30vh);
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 992px) {
  .modal-body {
    width: 80%;
  }
}

@media only screen and (max-width: 768px) {
  .modal-body {
    width: 90%;
  }
}

header {
  position: relative;
  background-color: black;
  height: 100vh;
  min-height: 25rem;
  width: 100%;
  overflow: hidden;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

header video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  transform: translateX(-50%) translateY(-50%);
}

header .container {
  position: relative;
  z-index: 2;
  margin-top: 5vh;
}

header .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.5;
  z-index: 1;
}

.btn-outline-light:hover {
  background-color: yellow !important;
}

.display-3 {
  font-weight: 400;
}

footer {
  padding: 30px 0;
  background: #222;
  color: #fff;
}

html,
body {
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrap {
  flex: 1 1;
}

.anchor-black {
  text-decoration: none;
  color: black;
}

.anchor-white {
  text-decoration: none;
  color: white;
}

.heading {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.btn-hover:hover {
  background-color: #cacaf5;
  color: #000;
}

.form-control:focus,
.btn:focus {
  box-shadow: 0 0 0 0;
}

.card {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.08), 0 0 6px rgba(0, 0, 0, 0.05);
  transition: 0.3s transform cubic-bezier(0.155, 1.105, 0.295, 1.12),
    0.3s box-shadow;
  /* cursor: pointer; */
}

.card-hover:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

